import { createWebHistory, createRouter } from "vue-router";

import Product from './components/DawooProduct.vue'
import Service from './components/DawooService.vue'
const routes = [
    {
    path: '/', 
    component: Product
    },
    {
        path:'/service',
        component: Service
    },

]   

const router = createRouter({
    history: createWebHistory(), routes,
   });
   export default router; 